<template>
  <div class="login-container">
    <nav-bar>
      <div slot="left"><img src="../assets/logo_mini.jpg"></div>
      <div slot="center"></div>
      <div slot="right"></div>
    </nav-bar>
    <el-card
    class="m-2 login-card text-center fs-xl">
      <!-- ativeprevent -->
      <el-form :rules="rules" @submit.native.prevent="login" ref="formRef" :model="model">
        <h2 class="text-center text-primary pb-2">   </h2>
        <el-form-item prop="username" label="">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item prop="password" label="">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item class="pt-3">
          <el-button type="primary" native-type="submit"></el-button>
          <el-button @click="resetForm('formRef')"></el-button>
        </el-form-item>
        <el-form-item>
          <span> <router-link class="text-primary" tag='span' to="/register"> </router-link></span>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  components: {
    NavBar
  },
  data(){
    return{
      model:{},
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
        ],
      }
    }
  },
  methods:{
    async login(){
      const res = await this.$http.post('login',this.model)
      // console.log(res.data)
      localStorage.token = res.data.token // token
      this.$router.push('/')
      this.$message({
        type:'success',
        message:''
      })
    },
    resetForm(formRef) {
      this.$refs[formRef].resetFields();
    }
  }
}
</script>

<style>
  .login-card{
    margin: 3rem auto;
    height:100vh;
    z-index: 999;
  }
</style>