<template>
  <div class="release">
    <nav-bar>
      <div slot="left"><img src="../assets/logo_mini.jpg"></div>
      <div slot="center"></div>
      <div slot="right"></div>
    </nav-bar>
    <el-form class="bg-white m-2 p-2"
    ref="formRef" 
    :model="model" 
    label-width="80px"
    @submit.native.prevent="save">
      <h2 class="text-center text-primary p-3">{{id ? " " : " "}}    </h2>
      <el-form-item prop="name" label="">
        <el-input placeholder="" 
         v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item prop="type" label="">
        <el-select v-model="model.type" multiple filterable  placeholder="" >
          <el-option  v-for="item in types"
            :key="item._id"
            :label="item.name"
            :value="item._id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="campus" label="">
        <el-select v-model="model.campus" placeholder="">
          <el-option label="" value=0></el-option>
          <el-option label="" value=1></el-option>
          <el-option label="" value=2></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="date" label="">
        <el-date-picker type="date" placeholder="" v-model="model.date" style="width: 100%;"></el-date-picker>
      </el-form-item>
      <el-form-item prop="originalPrice" label="">
        <el-input placeholder="" v-model="model.originalPrice"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          v-model="model.photo"
          multiple
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
          :on-success="afterUpload"
          >
          <!-- <div v-if="model.photo" v-for="item in model.photo">
            <img :src="item">
          </div> -->
          <!-- <i v-else class="el-icon-plus"></i> -->
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-form-item>
      <el-form-item prop="description" label="">
        <el-input placeholder="" 
        type="textarea" 
        :rows="5" 
        v-model="model.description">
        </el-input>
      </el-form-item>
      <el-form-item prop="price" label="">
        <el-input placeholder="" v-model="model.price"></el-input>
      </el-form-item>
      <el-form-item prop="tel" label="">
        <el-input placeholder="" v-model="model.tel"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" native-type="submit">{{id ? "" : ""}}</el-button>
        <el-button size="small" type="warning" @click="resetForm('formRef')"></el-button>
        <el-button v-if="id" size="small" type="danger" @click="deleteReleaseInfo"></el-button>
        <el-button v-if="id" size="small" type="info" @click="$router.back(-1)"></el-button>
      </el-form-item>
    </el-form>
    <div class="height"></div>
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  name: 'Release',
  props:{
    id:{
      type:String
    }
  },
  components: {
    NavBar
  },
  data(){
    return {
      model: {
          name: '',
          type: [],
          campus: '',
          date: '',
          originalPrice: '',
          description: '',
          price: '',
          tel: '',
          photo:[]
        },
        dialogImageUrl: '',
        dialogVisible: false,
        types:null,
        /* options:[],
        selectedOptions: [],
        defaultParams: {
            label: 'name',
            value: '_id',
            children: 'parent'
        } */
    }
  },
  computed:{
  },
  methods: {
    // 
    async afterUpload(res){
      this.model.photo.push(res.url)
    },
    // 
    handleRemove(file, fileList) {
      // console.log(file, fileList);
      const text = file.response.url
      const arr = this.model.photo
      for(let i = 0; i < arr.length; i++) {
          if (text == arr[i]) {
          arr.splice(i,1);
          }
        }
    },
    // 
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 
    async fetchCategories(){
      const res = await this.$http.get('/rest/categories')
      this.types = res.data
      // this.options = res.data
      // this.parents = this.types.filter(n=>!n.parent)
      // this.children = this.types.filter(n=>n.parent)
    },
    // 
    async save() {
      let res
      // id
      if(this.id){
        res = await this.$http.put(`rest/goods/${this.id}`,this.model)
      }
      // id
      else{
        res = await this.$http.post('/rest/goods',this.model)
      }
      // const res = await this.$http.post('/rest/goods',this.model)
      // 
      // this.$router.go(0); 
      this.$router.push('/')
      this.$message({
        type:'success',
        message:''
      })
    },
    // 
    resetForm(formRef) {
      this.$refs[formRef].resetFields();
    },
    async fetch(){
      const res = await this.$http.get(`rest/goods/${this.id}`)
      this.model = Object.assign({},this.model,res.data)
    },
    async deleteReleaseInfo(id){
      const res = await this.$http.delete(`rest/goods/${this.id}`)
      this.$router.push('/me')
    }
  },
  created(){
    this.fetchCategories()
    this.id && this.fetch() //  id  fetch() 
  },
}
</script>

