<template>
  <div class="demand">
    <nav-bar>
      <div slot="left"><img src="../assets/logo_mini.jpg"></div>
      <div slot="center"></div>
      <div slot="right"></div>
    </nav-bar>
    <el-form class="bg-white auto-hight m-2 p-2" 
      style="height:90vh;"
      ref="formRef"
      :model="model" 
      label-width="80px"
      @submit.native.prevent="save">
        <h2 class="text-center text-primary p-3">{{id ? " " : " "}}    </h2>
        <el-form-item prop="name" label="">
          <el-input placeholder="" v-model="model.name"></el-input>
        </el-form-item>
        <!-- <el-form-item prop="campus" label="">
          <el-select v-model="model.campus" placeholder="">
            <el-option label="" value=0></el-option>
            <el-option label="" value=1></el-option>
            <el-option label="" value=2></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item prop="description" label="">
          <el-input type="textarea" 
          :rows="8" 
          v-model="model.description"
          placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="tel" label="">
          <el-input placeholder=""  v-model="model.tel"></el-input>
        </el-form-item>
        <el-form-item prop="price" label="">
          <el-input placeholder="'100-500'" v-model="model.price"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" native-type="submit">{{id ? "" : ""}}</el-button>
          <el-button size="small" type="warning" @click="resetForm('formRef')"></el-button>
          <el-button v-if="id" size="small" type="danger" @click="deleteDemandInfo"></el-button>
          <el-button v-if="id" size="small" type="info" @click="$router.back(-1)"></el-button>
        </el-form-item>
    </el-form>
    <!-- <div class="height"></div> -->
  </div>
