<script>
import NavBar from '../components/NavBar'
export default {
  name: 'Demanfd',
  components: {
    NavBar
  },
  props:{
    id:{
      type:String
    }
  },
  data(){
    return {
      model: {
          name: '',
          campus: '',
          description: '',
          price: '',
          tel: '',
        },
        dialogImageUrl: '',
        dialogVisible: false,
        types:null,
    }
  },
  methods:{
    // 
    resetForm(formRef) {
      this.$refs[formRef].resetFields();
    },
    async save(){
      let res
      if(this.id){
        res = await this.$http.put(`rest/demands/${this.id}`,this.model)
      }
      else{
        res = await this.$http.post('/rest/demands',this.model)
      }
      this.$router.push('/square')
      this.$message({
        type:'success',
        message:''
      })
    },
    async fetch(){
      const res = await this.$http.get(`rest/demands/${this.id}`)
      this.model = Object.assign({},this.model,res.data)
    },
    async deleteDemandInfo(id){
      const res = await this.$http.delete(`rest/demands/${this.id}`)
      this.$router.push('/me')
    }
  },
  created(){
    this.id && this.fetch() //  id  fetch() 
  },
}
</script>

