<template>
  <div class="login-container">
    <nav-bar>
      <div slot="left"><img src="../assets/logo_mini.jpg"></div>
      <div slot="center"></div>
      <div slot="right"></div>
    </nav-bar>
    <el-card header="" class="m-2 login-card text-center fs-xl">
      <!-- ativeprevent -->
      <el-form
       :rules="rules" 
       @submit.native.prevent="save" 
       ref="formRef" 
       :model="model"
       :label-position="labelPosition">
       <h2 class="text-center text-primary pb-2">   </h2>
        <el-form-item prop="username" label="">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item prop="tel" label="">
          <el-input v-model="model.tel"></el-input>
        </el-form-item>
        <el-form-item prop="password" label="">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item prop="password2" label="">
          <el-input type="password" v-model="model.password2"></el-input>
        </el-form-item>
        <el-form-item class="pt-3">
          <el-button type="primary" native-type="submit"></el-button>
          <el-button @click="resetForm('formRef')"></el-button>
        </el-form-item>
        <el-form-item>
          <span> <router-link class="text-primary" tag='span' to="/login"> </router-link></span>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  components: {
    NavBar
  },
  data(){
    // 
    let confirmpasswordCheck = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error(''))
      } else if (value !== this.model.password) {
        return callback(new Error(''))
      } else {
        return callback()
      }
    }
    // 
      const checkMobile = (rule,value,cb) => {
        // 
        const regMobile = /^1(3[0-9]|4[5,7]|5[0,1,2,3,5,6,7,8,9]|6[2,5,6,7]|7[0,1,7,8]|8[0-9]|9[1,8,9])\d{8}$/
        if(regMobile.test(value)){
          // 
          return cb()
        }
        cb(new Error(''))
      }
    return{
      model:{},
      labelPosition: 'right',
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
        ],
        tel:[
            { required: true, message: '', trigger: 'blur' },
             { validator: checkMobile, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
        ],
        password2: [
          {
            required: true,
            validator: confirmpasswordCheck,
            trigger: 'blur'
          }
        ],
      }
    }
  },
  methods:{
    async save(){
      const res = await this.$http.post('/register',this.model)
      this.$router.push('/login')
      this.$message({
        type:'success',
        message:''
      })
    },
    resetForm(formRef) {
        this.$refs[formRef].resetFields();
    },
  }
}
</script>

<style>
  .login-card{
    margin: 1rem auto;
  }
</style>